/* ==========================================================================
	Imports
 ========================================================================== */
@import
'variables',
'reset',
'animations',
'swiper/swiper-master',
'swiper',
'accordion',
'404',
'buttons',
'blog',
'blocks',
'qpc-menu-toogle',
'qpc-helpers',
'qpc-infografica',
'wp-native-gallery',
'pre-header',
'header',
'qpc-col',
'qpc-dett-scheda',
'forms',
'footer',
'qpc-loading-screen',
'qpc-overlay',
'qpc-insta-grid',
'jquery.fancybox.min.css';

@font-face {
  font-family: "neue_machinaregular";
  font-weight: normal;
  font-style: normal;
  src: url($fonts_dir + 'neuemachina-regular-webfont.woff2') format("woff2"),
  url($fonts_dir + 'neuemachina-regular-webfont.woff') format("woff")
}

@font-face {
    font-family: 'neue_machinaultrabold';
    font-weight: normal;
    font-style: normal;
    src: url($fonts_dir + 'neuemachina-ultrabold-webfont.woff2') format("woff2"),
    url($fonts_dir + 'neuemachina-ultrabold-webfont.woff') format("woff")
}

@font-face {
    font-family: 'sangbleu_empireregular';
    src: url('../fonts/sangbleuempire-regular-webfont.woff2') format('woff2'),
         url('../fonts/sangbleuempire-regular-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

body{
  margin: 0;
  padding: 0;
  @include tipografia(sans-serif);
  color: $text_color;
  overflow-x: hidden;
  background: $color_white;
}

.input-wrap-header input,
.swiper-head .wrap-swiper-title span,
.titleHome span,
h1, h2,  h4, h5, h6  {
  @include tipografia();
}

img{
  width: 100%;
}
a{
  text-decoration: none;
  color: inherit;
}

/* ==========================================================================
	@mixin
/* ========================================================================== */


@mixin box-shadow-blur($blur-value) {
  box-shadow: 0px 0px $blur-value 0px rgba(0, 0, 0, 0.20);
}

.tabs p {
@include tipografia(sans-serif);
  .quote {
  @include tipografia();
  }
}

.search-form-header .input-wrap-header input,
.article-title.news-home {
  font-size: $size_base
}

b, strong {
  font-family: $font_sans_serif_bold;
}


/* ================= */


  .breadcrumb{
    margin: 20px 0;
  }

.retino {
  z-index: 2;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: url($images_dir + 'pattern.png') repeat;
}


/* ==========================================================================
	Pagination
/* ========================================================================== */

.navigation {
  display: block;
  margin: 50px auto 50px auto;
  text-align: center;
  color: $color_white;
  float: left;
  clear: both;
  width: 100%;
  li, li.active a, li.disabled{
    display: inline;
    a, &:hover{
      background-color: $theme_color;
      border-radius: 3px;
      cursor: pointer;
      padding: 12px;
      padding: 0.75rem;
    }
  }
}

.navigation li a:hover,
.navigation li.active a {
  background-color: $color_white;
  border: 1px solid $theme_color;
  color: $theme_color;
}

/* ========================================================================== */

.excerpt {
  font-weight: bold;
}


h1.title-style-theme-color {
  &:after {
    background: $color_white !important;
    color: $color_white;
    content: " ";
  }
}


.category_description{
  max-width: 550px;
  margin: 0 auto 90px auto;
  padding: 20px;
  p{
  font-size: 18px !important;
  }
}

.row-news{
  margin: 0 auto 110px
}

/* ==========================================================================
   Homepage section
 ========================================================================== */

.page-template-homepage{
 #wrap-custom-post{
   margin-top: 20%;
 }
}

/* ==========================================================================
 	Welcome section
 ========================================================================== */

.all-results {
  display: flex;
  align-items: center;
  background: $theme_color !important;

  a {
    color: $color_white;
    margin: 0 auto;
    text-align: center;
    font-weight: bold;
  }
}

/* ========================================================================*/
/* ! LAYOUT */
/* ========================================================================*/

.part-extra-padding {
  margin: 70px auto;
}

.breadcrumb {
  font-size: $size_base;
  span{
    font-size: $size_base;
  }
}


/* ==========================================================================
	Archivio categoria
 ========================================================================== */

 h2.article-title{  text-align: left;}
 h3.article-title{  text-align: center;}

/* ==========================================================================
	Pagina dettaglio
 ========================================================================== */


/* ==========================================================================
	Pagina dettaglio
 ========================================================================== */

.post-type-archive, .post-template, .blog, .search-results {
  ul {
    & h2, & h3 {
      font-family: $font_sans_serif_regular;
      color: $color_black;
    }

    & .img-container img {
      margin: 0 auto;
      display: block;
    }

    & .titleCustomPost {
      font-size: 63px !important;
      color: $color_link !important;
      font-family: $font_sans_serif_regular !important;
      display: table;
      text-align: center;
      font-weight: 900;
      line-height: 61px;

      img {
        display: block;
        margin: 0 auto;
        text-align: center;
      }
    }

    h3 {
      font-size: 18px;
      margin-bottom: 0;
      font-weight: 700;
    }

  }
}

/* ==========================================================================
	Box partners
 ========================================================================== */

.wrap-partners{
  ul{
    list-style-type: none;
    max-width: 500px;
    margin: 0;
    padding: 0;
    li{
      display: inline-block;
      margin: 10px 5px;
    }
  }
}

