   }
 }


@media only screen and (min-width: 1024px) {

  .row-news {
    max-width: 1200px;
    width: 100%;
    margin: 0 auto 50px;
    img {
      margin: 0 auto;
      display: block;
    }
    & .article-title {
      text-align: center;
    }
    & .col-md-4 {
      margin: 0 0 40px 0;
    }
  }

}


@media only screen and (max-width: 1366px) {

}

@media only screen and (max-width: 1024px) {

  .welcome-title {
    h1 {
      font-size: 40px !important;
      line-height: 1em;
      font-weight: 900;
    }
    h3 {
      font-size: 22px !important;
      line-height: 1em;
    }
   }
  }

@media only screen and (max-width: 960px) {

  h1.title-style{
    font-size: 2.5rem;
  }

}

@media only screen and (max-width: 768px) {
  .qpc-container{
    padding: 20px;
  }
  .breadcrumb{
    display: none;
  }

  .wrap-archive-products {
    h3 {
      font-size: 16px;
      text-align: center;
      margin: 0 auto;
    }
  }

  .article-title {
    text-align: center;
  }

  .header-medium-heigth {
    min-height: 200px;
  }

  .header-medium-heigth .titleHome {
    font-size: 8vw;
    line-height: 0.9em;
    width: 100%;
    h1 {
      font-size: 4vw;
      margin-top: 20px;
    }
  }

  .wrap-text {
    max-width: 100%;
    margin: 0 auto;
  }

  .yt-player {
    width: 100%;
    min-height: 155px;
  }


  .wrap-autocomplete {
    input {
      font-size: 12px;
      &:first-child {
        margin: 15px 0 10px 0;
      }
    }
  }

  .wrap-box {
    .article-title {
      left: auto;
      font-size: 20px;
      width: 100%;
      bottom: 10px;
    }
  }
}

@media only screen and (max-width: 568px) {

  .wrap-partners{
    ul li{
      margin: 5px !important;
    }
    .qpcRow .qpc-col-33{
      width: 29.333%;
    }
    .qpcRow .qpc-col-33:first-child,
    .qpcRow .qpc-col-33:last-child {
      padding: 0;
    }
  }

}
